.fontSizeForMainPicture {
  flex-wrap: wrap;
  white-space: nowrap;
  overflow: hidden;
  color: #fff;
  height: 100vh;
  font-size: 30vh;
  line-height: 110%;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.fontSizeText1 {
  animation-name: textRotating;
  animation-duration: 200s;
  animation-iteration-count: infinite;
  animation-delay: 0s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  margin-left: 17%;
  
}
.fontSizeText5 {
  animation-name: textRotating5;
  animation-duration: 180s;
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  margin-left: 17%;
}
.fontSizeText2 {
  animation-name: textRotating2;
  animation-duration: 200s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  position: absolute;
}
.fontSizeText3 {
  animation-name: textRotating3;
  animation-duration: 250s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-delay: 0s;
  margin-left: -105vw;
  /* animation: reverse; */
}
.fontSizeText4 {
  animation-name: textRotating4;
  animation-duration: 450s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-delay: 3.5s;
  position: absolute;
}
@keyframes textRotating {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(-5%);
  }
}
@keyframes textRotating5 {
  0% {
    transform: translateX(-120%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes textRotating2 {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes textRotating3 {
  0% {
    transform: translateX(-0%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes textRotating4 {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(0%);
  }
}

